import sendMsgCom from '@/components/send-msg/send-msg.vue'; 
export default {
	components: {
		sendMsgCom
	},
	data() {
		return {
			title: 'Hello',
			username: '',
			msg_code: '',
			yzmstate: 0,
			yzmhtml:'获取验证码',
			activeColor: '#2CC17B'
			//#2CC17B greyColor:'#c5cddb'
		}
	},
	onLoad() {
		console.log(11111111)
	},
	methods: {
		clearInput(val) { // 清除输入框
			if (val == 'username') {
				this.username = '';
				console.log('清除')
			}
		},
		login() { // 登录操作
			// 打开动画
			uni.showLoading({
				title: '登录中...',
				mask: true
			});
			uni.request({
				url: this.$serverUrl + '/api/login/loginByMsg',
				method: 'POST',
				data: {
					phone_num: this.username,
					msg_code: this.msg_code
				},
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('请求失败', ret);
						return;
					}
					if (ret.data.code == 0) {
						// 取数据并赋值
						uni.setStorageSync('userinfo', ret.data.data);
						// 气泡提示
						uni.showToast({
							title: ret.data.message,
							duration: 2000
						});
						// 关闭动画
						uni.hideLoading();
						// 跳转到首页
						uni.reLaunch({
							url: '../index/index'
						});
					} else {
						// 关闭动画
						uni.hideLoading();
						// 气泡提示
						uni.showToast({
							title: ret.data.message,
							icon: 'none',
							duration: 2000
						});
					}
				}
			});
		}
	}
}
